@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap');
.App {
  text-align: center;
  background-color: #fff;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.topBar {
  width: 100%;
  height: 50px;
  background-color: #71BF44;
}
.mainContainer{
  margin: 0 auto;
  width: 90%;
}
.logo{
  width: 300px;
  height: 226px;
}
nav.no-mobile{
  margin: 0 auto;
  width: 90%;
  height: 180px;
  margin-bottom: 10px;
  text-align: left;
  display: inline-block;
}
nav.no-mobile ul{
  float: right;
  display: flex;
  right: 0px;
  justify-content: space-around;
  width: 45%;
  margin-top: 50px;
}
nav.no-mobile li {
  list-style-type: none;
  font-size: 30px;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #333231;
}
nav.no-mobile li a{
  text-decoration: none;
  color: #333231;
}
nav.no-mobile li a:hover{
  color: #71BF44;
}
nav.no-mobile li a.active{
  color: #71BF44;
}
div.active-line{
  width: 100%;
  height: 5px;
  background-color: #71BF44;
  display: none;
}
a.active div{
  display: block;
}
#menu-bars{
  background-image: url(./images/menu_lines.svg);
  width: 49px;
  height: 24px;
  position: absolute;
  right: 5%;
  top: 13px;
  display: none;
}
.navMobile{
  display: none;
}
.navMobile ul{
  height: 208px;
  display: none;
  position: relative;
  width: 100%;
  margin: 0px;
  padding: 0px;
  background-color: #333231;
}
.navMobile ul.active{
  display: block;
}
.navMobile li{
  list-style-type: none;
  font-size: 23px;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  height: 52px;
  line-height: 52px;
}
.navMobile a{
  position: absolute;
  width: 100%;
  left: 0;
  text-align: center;
  color: #fff;
  text-decoration: none;
}
.navMobile a.active{
  background-color: #D1D2D3;
  color: #333231;
}
footer{
  margin-top: 0px;
  height: 60px;
  font-size: 15px;
}
.footer_logo{
  width: 40px;
}

/**************** HOME PAGE **************************/
header{
  margin-bottom: 80px;
}
#penta1{
  margin-bottom: 5px;
}
h1{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 700;
  font-size: 31px;
  margin: 0;
  color: #333231;
  text-transform: uppercase;
}
h2{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 700;
  font-size: 26px;
  color: #333231;
  text-transform: uppercase;
}
h3{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
  font-size: 24px;
  margin: 0;
  color: #333231;
}
.columnsContainer{
  display: flex;
  justify-content: space-between;
}
.column{
  text-align: left;
  width: 40%;
}
.column1{
  float: left;
}
.column2{
  float: right;
}
.column h2{
  margin-top: 40px;
  margin-bottom: 0px;
}
.column p{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
  font-size: 25px;
  margin: 10px 0px;
}
#penta2{
  background-image: url(./images/penta2.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 306px;
  max-width: 450px;
}
footer{
  background-color: #333231;
  color: #fff;
  height: 120px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 5%;
  margin-top: 30px;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 22px;
  font-weight: 300;
}
.footer_logo{
  width: 65px;
  margin-left: 20px;
}

@media screen and (max-width: 1200px) {
  nav.no-mobile li {
    font-size: 24px;
  }
  h1{
    font-size: 29px;
  }
  h2{
    font-size: 28px;
  }
  h3{
    font-size: 26px;
    margin: 3px 0;
  }
  .column p{
    font-size: 22px;
  }
}
@media screen and (max-width: 940px) {
  nav.no-mobile li {
    font-size: 20px;
  }
  .logo{
    width: 270px;
    height: 203px;
  }
  h1{
    font-size: 23px;
  }
  h2{
    font-size: 22px;
  }
  h3{
    font-size: 18px;
  }
  .column p{
    font-size: 16px;
  }
}
@media screen and (max-width: 769px) {
  #menu-bars{
    display: block;
  }
  .navMobile{
    display: block;
  }
  nav.no-mobile{
    display: none;
  }
  nav{
    margin-bottom: 10px;
    text-align: center;
  }

  nav li {
    font-size: 16px;
  }
  .logo{
    width: 220px;
    height: 165px;
  }
  header{
    margin-bottom: 50px;
  }
  .columnsContainer{
    display: block;
  }
  .column{
    width: 100%;
  }
  h1{
    font-size: 25px;
  }
  h2{
    font-size: 23px;
  }
  h3{
    font-size: 19px;
    margin: -1px 0;
  }
}
/********************************************************/
/****************** Crear Page **************************/

.header-left{
  width: 50%;
  text-align: left;
}
.header-right{
  width: 50%;
  text-align: right;
}
header.two-columns{
  display: flex;
  align-items: center;
}
.image-header{
  width: 300px;
  margin-top: 0px;
}

@media screen and (max-width: 940px) {
  .image-header{
    width: 70%;
    margin-top: 30px;
  }
}

@media screen and (max-width: 769px) {
  .header-left, .header-right{
    width: 100%;
    text-align: center;
  }
  header.two-columns{
    display: block;
  }
  .image-header{
    width: 220px;
    margin-top: 30px;
  }
}

/********************************************************/
/****************** Agregar Page **************************/

.linea-divisoria{
  background-color: #333231;
  width: 100%;
  height: 3px;
}
#lista-cantos{
  margin-top: 30px;
}
#presentacion-cantos{
  margin-top: 48px;
  min-height: 559px;
  border: 7px solid #333231;
}
input[type="text"] {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 17px;
  font-weight: 300;
  height: 45px;
  width: 300px;
  border-radius: 0 20px 20px 0;
  border-width: 1px;
  border-color: transparent;
  border-left: none;
  max-width: 75vw;
}
#crearContainer button{
  width: 50px;
  height: 49px;
  position: relative;
  top: -6px;
  background-color: #fff;
  border: 1px solid transparent;
  border-right: none;
  border-radius: 20px 0 0 20px;
}
#buscar-container{
  width: 355px;
  position: relative;
  box-shadow: 0px 5px 5px -6px;
  height: 49px;
  border-radius: 20px;
  margin-bottom: 15px;
  max-width: 90vw;
  border: 2px solid #707070;
}
#borrar-termino{
  position: absolute;
  right: 15px;
  top: 16px;
  width: 16px;
  height: 16px;
  display: none;
}
#crearContainer form{
  margin-top: 30px;
}
.titulo-container{
  display: flex;
  align-items: center;
  width: 100%;
  height: 34px;
  justify-content: space-between;
  border: 1px solid #333231;
  margin: -1px;
}
.titulo-container:hover{
  background-color: rgba(113,191,68,0.2);
}
h4{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300;
  font-size: 24px;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 65vw;
  overflow: hidden;
}
.iconos{
  width: 75px;
  display: flex;
  justify-content: space-between;
  padding: 0 3px;
  align-items: center;
}
.icono:hover{
  opacity: 0.5;
  cursor: pointer;
}
.icono-ver{
  background-image: url(./images/icono_ver.svg);
  width: 35px;
  height: 18px;
  background-size: contain;
}
.icono-agregar{
  background-image: url(./images/icono_agregar.svg);
  width: 23px;
  height: 23px;
  background-size: contain;
}
.icono-cerrar{
  background-image: url(./images/icono_cerrar.svg);
  width: 23px;
  height: 23px;
  background-size: contain;
}
.icono-buscar{
  background-image: url(./images/icono_buscar.svg);
  width: 25px;
  height: 25px;
  background-size: contain;
  position: absolute;
  top: 11px;
  left: 7px;
}
.icono-crear{
  background-image: url(./images/icono_crear.svg);
  width: 65px;
  height: 65px;
  background-size: contain;
}
.icono-descargar{
  background-image: url(./images/icono_descargar.svg);
  width: 72px;
  height: 65px;
  background-size: contain;
}
.icono-borrar-todo{
  background-image: url(./images/icono_borrar_todo.svg);
  width: 59px;
  height: 65px;
  background-size: contain;
}
.icono-arriba{
  background-image: url(./images/icono_arriba.svg);
  width: 19px;
  height: 23px;
  background-size: contain;
}
.icono-abajo{
  background-image: url(./images/icono_abajo.svg);
  width: 19px;
  height: 23px;
  background-size: contain;
}
.preview-container{
  position: fixed;
  top: 0px;
  left: 50%;
  background: #fff;
  height: 90vh;
  margin: 20px 20px;
  padding: 20px;
  overflow: auto;
  transform: translate(-50%, 0px);
}
@media screen and (max-width: 769px) {
  .preview-container{
    position: fixed;
    top: 0px;
    left: 50%;
    background: #fff;
    height: 90vh;
    margin: 20px 20px;
    padding: 20px;
    overflow: auto;
    transform: translate(-50%, 0px);
  }
}
#himno-overlay{
  background: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
}
.estrofa{
  font-size: 26px;
  font-family: 'Roboto Condensed';
  font-weight: 400;
  width: 420px;
  margin: 0 auto;
  padding: 20px 0;
}
.visible{
  display: block!important;
}
#close-preview{
  position: absolute;
  right: 5px;
  top: 5px;
  width: 20px;
  height: 20px;
}
:focus-visible{
  outline: none;
}
#iconos-presentacion{
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.oracion{
  background-color: #71BF44;
  color: #fff;
}
.imagen{
  background-color: #ACD25D;
  color: #fff;
}
.titulo-selected{
  background-color: #D1D2D3;
  color: #FFFFFF;
}
.icono-selected{
  opacity: 0.5;
}
.iconos-ele{
  width: 25px;
  display: flex;
  justify-content: space-between;
  padding: 0 3px;
  align-items: center;
}
.titulo h4{
  margin: 0 0 0 5px;
}
#lista-elementos{
  margin-top: 0px;
  margin-left: 0px;
  background-color: rgba(255,255,255,0.9);;
  z-index: 7;
  position: absolute;
  width: 200px;
  display: none;
}
#paginas{
  display: flex;
  justify-content: center;
  margin: 15px 0;
}
#paginas h3{
  min-width: 130px;
  text-align: center;
}
#flecha-siguiente .icono-abajo{
  transform: rotate(-90deg);
  display: block;
  margin: 0px 50px;
}
#flecha-anterior .icono-abajo{
  transform: rotate(90deg);
  display: block;
  margin: 0px 50px;
}
#flecha-siguiente .icono-abajo:hover{
  opacity: 0.5;
}
#flecha-anterior .icono-abajo:hover{
  opacity: 0.5;
}